.Socket{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 150px;
    height: 100%;
    font-size: x-small;
}   
.row{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
}
.column{
    
    display: flex;
    width: 50%;
    justify-content: space-between  ;
    align-items: center;
}
.columnClock{
        
    display: flex;
    width: 50%;
    justify-content: center  ;
    align-items: center;
}
.columnClock>img{
    width: 10px;
    height: 10px;
}